.layout-parent {
    display: flex;
    height: 100%;
    width: 100%;
    height:100vh;
    gap:10px;
}
.body{
    width: 100%;
}

.header{
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.486);
    width: 100%;
    padding : 20px;
}

.sidebar{
    width: 300px;
    background-color: var(--secondary);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.active.menu-item{
    border: 2px solid white;
    border-radius: 5px;
}
.menu-item{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 15px;
    width: 100%;
    cursor: pointer;
    transition: 0.2s;
    gap:15px;
}

.menu-item i{
    font-size: 20px;
    margin-right: 10px;
    color: white;
}
.menu-item span{
    color: white;
    font-size: 18px;
}
.content{
    padding: 10px;
}